import { IsInt, IsString, IsNotEmpty, IsNumber, IsOptional, IsDate } from "class-validator";

export class TimesheetDTO {
    @IsOptional()
    @IsInt()
    id?: number;

    @IsInt()
    @IsNotEmpty()
    userId: number;

    @IsString()
    @IsNotEmpty()
    projectName: string;

    @IsInt()
    @IsNotEmpty()
    projectId: number;

    @IsString()
    @IsNotEmpty()
    description: string;

    @IsNumber()
    @IsNotEmpty()
    hours: number;

    @IsString()
    @IsNotEmpty()
    status: string;

    @IsDate()
    @IsNotEmpty()
    date: Date;
}
